import React,{useState} from 'react';
import loginService from './services/login'
import UsersService from './services/users'
import LoginForm from './components/LoginForm'
import BlogAddingForm from './components/BlogAddingForm'
import Blog from './components/Blog'
import blogService from './services/blogs'
import UserBlogTab from './components/UserBlogTab'

function App() {
  const [user, setUser] = useState(null)
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [blogs, setBlogs] = useState([])

  const handleLogin = async (event) => {
    event.preventDefault()
    try{
      const loggedUser = await loginService.login({
        username, password
      })

      window.localStorage.setItem('loggedUser', JSON.stringify(loggedUser))

      blogService.setToken(loggedUser.token)
      setUser(loggedUser)
      setUsername('')
      setPassword('')
      setBlogs(loggedUser.blogs)
    }catch(exception) {
      
    }
  }
  
  const conditionalLoginRendering = () => {
    if (user===null){
      return (
        <LoginForm handleLogin={handleLogin} 
          username={username} password={password} 
          setUsername={setUsername} setPassword={setPassword}/>
      )
    }else {
      return (
        <div>
          <UserBlogTab blogs={blogs} setUser={setUser}/>
          <BlogAddingForm blogs={blogs} setBlogs={setBlogs}/>
        </div>
      )
    }
  }

  return (
    <div className="App">
      {conditionalLoginRendering()}
    </div>
  );
}

export default App;
