import React from 'react'
import Blog from './Blog'

const UserBlogTab = ({blogs, setUser}) => {
    return (
        <div>
          <h3>Blogs</h3>        
          <ul>
            {blogs.map(blog => <Blog blog={blog}/>)}
          </ul>
          <button onClick={() => setUser(null) && localStorage.removeItem('loggedUser')}>
            logout
          </button>
        </div>
    )
}

export default UserBlogTab
