import React from 'react'

const LoginForm = ({handleLogin,username,password,setUsername,setPassword}) => {
    return (
    <form onSubmit={handleLogin}>
        <h3>Login to application</h3>
        login
        <input value={username} type="text" name="Username" onChange={({target}) => setUsername(target.value)}/>
        <br/>
        password
        <input value={password} type="password" name="Password" onChange={({target}) => setPassword(target.value)}/>
        <br/>
        <button type="submit">login</button>
    </form>
    )
}

export default LoginForm